import fb from '../fb';
import {$, Component, View, State, Router} from 'farmy';

const RootView = new View(`

<div class="top">
	<img class="header" src="./header.svg" alt="fan assists header">
	<p class="hero-text">WANT TO DOMINATE DAILY <br> FANTASY IPL MATCHES <br> <span>LIKE A PRO?</span></p>
	<div class="info">INFO</div>
	<form>
		<span class="material-icons">call</span>
		<input type="tel" placeholder="Enter mobile number" required>
		<button type="submit">REGISTER</button>
	</form>
</div>

<div class="middle">
	<div class="screens">
		<img class="left-screen" src="./expertanalysispage.jpg" alt="fan assists expert analysis screen">
		<img class="home-screen" src="./homepage.jpg" alt="fan assists home screen">
		<img class="right-screen" src="./playerprofilepage.png" alt="fan assists player profile screen">
	</div>
	<p class="text">Gain an <span>unprecendented edge</span> with curated <br> fantasy cricket analysis at your fingertips</p>
</div>

<div class="bottom">
	
	<div class="cards">
		<div class="card">
			<img src="./wincat.png" alt="fan assists win cat">
			<h2>FANTASY FOCUSED</h2>
			<p>AQUIRE STADIUM, TOSS & PLAYER INSIGHTS LIKE NEVER BEFORE</p>
		</div>
		<div class="card big">
			<img src="./coscocat.png" alt="fan assists cosco cat">
			<h2>SIMPLIFIED DECISIONS</h2>
			<p>DATA DRIVEN DECISION MAKING TO CREATE WINNING TEAM</p>
		</div>
		<div class="card">
			<img src="./venuecat.png" alt="fan assists vemue cat">
			<h2>COMMUNITY DRIVEN</h2>
			<p>GET ACCESS TO A COMMUNITY OF FANTASY ENTHUSIASTS</p>
		</div>
	</div>
	
</div>

<div class="lower">
	<img src="./crown.png" alt="fan assists premium crown image">
	<p>Register for our app and become <br> <span>Fan Assists exclusive member</span></p>
	<button> Register</button>
</div>

<footer>
	<div class="inner">
		<p class="fanassists">2021 - <span>FANASSISTS.COM</span> ©</p>
		<a href="https://chat.whatsapp.com/K9bP6fwScSL0DGoaGplTFA" target="_blank">
			<span class="material-icons">whatsapp</span>
			<p>JOIN COMMUNITY</p>
		</a>
	</div>
</footer>

`);

class Root extends Component {
	
	constructor() {
		super();
	}
	
	view() {
		return RootView.get();
	}
	
	load() {
		const $form = $('.top form');
		const $input= $form.$('input');
		const $btn = $form.$('button');
		const $info = $('.top .info');
		let formActive = true;
		$form.on('submit', async (e) => {
			e.preventDefault();
			if (formActive) formActive = false;
			else return;
			
			try {
				const phone = $input.el.value;
				
				if (phone.length < 4) throw Error('Incorrect phone number');
				
				$btn.innerHTML('<div class="loader"></div>');
				
				const x = await fb.register(phone);
				console.log(x);
				
				$btn.innerHTML('Register');
				formActive = true;
				$input.el.value = '';
				
				$info.innerHTML('Successfully Registered');
				$info.rmClass('error').addClass('active');
				
			} catch (e) {
				
				$btn.innerHTML('Register');
				formActive = true;
				
				$info.innerHTML(e);
				$info.addClass('error').addClass('active');
				
			}
		});
		
		const $topSection = $('.top');
		const $lowerBtn = $('.lower button');
		$lowerBtn.on('click', () => {
			$topSection.el.scrollIntoView({
				behavior: "smooth"
			});
		});
	}
	
}

export default Root;
