import Firebase from "firebase/app";
import 'firebase/analytics';
import 'firebase/database';

const firebaseConfig = {
	apiKey: "AIzaSyCtml0OJTiB7jrF7nhOSYrHFOQy7oafArY",
	authDomain: "fanassists-landing.firebaseapp.com",
	projectId: "fanassists-landing",
	storageBucket: "fanassists-landing.appspot.com",
	messagingSenderId: "674590193050",
	appId: "1:674590193050:web:559b54d05ab54f4a563623",
	measurementId: "G-84EPWPCY78",
	databaseURL: "https://fanassists-landing-default-rtdb.firebaseio.com/"
};

const firebase = Firebase.initializeApp(firebaseConfig);
const analytics = firebase.analytics();
const database = firebase.database();

const RegisterCollection = database.ref('registers');

const getDataSnap = (snap) => ({id: snap.id, ...snap.data()});
const getDataSnapshot = (snapshot) => snapshot.docs.map(snap => getDataSnap(snap));

const register = async (phone) => {
	try {

		const push = RegisterCollection.push();
		
		return await push.set({
			phone: phone,
			created: Date.now()
		});

	} catch (e) {
		throw e;
	}
};

export default {
	Firebase, firebase, database,getDataSnap, getDataSnapshot,
	register
};
